@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Redressed&display=swap);
nav{
    background:black;
    width:80%;
    margin:0 auto;
    display: flex;
    align-items: center;
}

ul.nav-items {
	margin: 0;
	padding: 0;
	display: flex;
	justify-content: center;
	flex: 2 2;
}

ul.nav-icons{
    margin:0;
    padding:0;
    display: flex;
    flex: 1 1;
    justify-content: flex-end;
    margin-right: 10px;
}

li{
    list-style:none;
}

.logo {
	font-family: 'Redressed', cursive;
	font-size: 25px;
	flex: 1 1;
}

nav a{
    color:white;
    text-decoration:none;
    padding:10px 15px;
    display:block;
}

nav a:hover{
    color:black;
    background:white;
}

.item-count{
    margin-top: -6px;
    position: absolute;
}
footer{
    display: flex;
    justify-content: center;
}
footer > p{
    font-weight: 100;
    font-size: 14px;
}
.products{
    display: flex; /* block level display */
    flex-wrap: wrap; /* wrap container so that flex items don't overflow */
}

.product-card{
    margin: 1.5%;
    flex-grow: 1; /* consume the remaining white space */
    flex-basis: 30%; /* set initial width */
    padding: 20px 0;
    box-shadow: 0 2px 6px rgba(0,0,0,.075);
    border-radius: 6px;
    position: relative;
}

.product-image img {
    max-width: 100%;
    margin: 0 auto;
    display: flex; /* block level display */
}

.product-info{
    text-align: center;
}

.product-detail{
    display: flex; /* block level display */
    flex-wrap: wrap; /* wrap container so that flex items don't overflow */
}

.product-detail-image{
    flex-grow: 1;
    flex-basis: 45%;
}

.product-detail-image img{
    margin: 0 auto;
    display: flex;
}

.product-detail-info{
    flex-grow: 1;
    flex-basis: 50%;
    padding: 25px;
}

.lead{
    font-size: 22px;
}

.add-cart-btn, .checkout{
    background: black;
    color: white;
    font-size: 12px;
    padding: 10px;
    text-transform: uppercase;
    border: 2px solid black;
    margin: 5px;
}

.add-cart-btn:hover{
    background: white;
    color: black;
    border: 2px solid black;
    
}

.remove-cart-btn {
	background: rgba(0,0,0,0.7);
	color: white;
	border: none;
	font-size: 12px;
	padding: 10px;
	text-transform: uppercase;
	border: 2px solid rgba(0,0,0,0.2);
	margin: 5px;
	font-weight: bold;
}

.view-detail-btn, .reset{
    background: white;
    color: black;
    font-size: 12px;
    padding: 10px;
    text-transform: uppercase;
    border: 2px solid black;
    margin: 5px;
}

.view-detail-btn:hover{
    background: black;
    color: white;
}

.view-detail-btn a{
    text-decoration: none;
}

.favorite-btn{
    position: absolute;
    top: 20px;
    right: 20px;
    font-size: 25px;
    cursor: pointer;
}

.ri-heart-fill{
    color: red;
}

@media (max-width: 780px) {
    .product-card {
      flex: 1 1 45%; /* consume the remaining white space & initial width set to 45% */
    }
}

@media (max-width: 480px) {
    .product-card {
      flex: 1 1 90%; /* consume the remaining white space & initial width set to 90% */
    }
}
.cart-section{
    width: 80%;
    display: block;
    margin: 0 auto;
}

.cart-detail{
    display: flex;
    align-items: center;
    justify-content: center;
}

.product-info-col{
    flex: 1 1;
    text-align: center;
}

.product-info-col img{
    width: 150px;
}

.product-info-col h5{
    margin: 3px 0;
}

.product-qty-col{
    flex: 1 1;
    text-align: center;
}

.product-counter-col{
    flex: 1 1;
    text-align: center;
}

.product-counter-col button{
    background: transparent;
    border: none;
}

.product-counter-col button i{
    font-size: 25px;
}

.summary-detail {
	display: block;
	flex-grow: 1;
	border: 3px solid black;
	width: 30%;
	margin: 30px auto;
    text-align: center;
    padding: 25px 10px;
}

.warn-txt{
    color: red;
}
.wishlist-detail{
    display: flex;
    align-items: center;
    justify-content: center;
}

.undofavorite-text{
	position: relative;
	top: -8px;
}
body, button{
  font-family: 'Montserrat', sans-serif;
}

button{
  cursor: pointer;
}

.about-page{
  text-align: center;
  margin: 80px 0;
}
